import { TestAdapter } from '@oracle/oraclejet-testing/UNSAFE_TestAdapter';
import { byCss } from '@oracle/oraclejet-testing/UNSAFE_Locators';

export class NavigationListAdapter extends TestAdapter {
  /**
   * Select the NavigationListItem identified by a locator
   * @param key The key of the NavigationListItem to select
   * @returns Promise<void>
   */
  async doSelect<K extends string | number>(key: K) {
    const driver = this.getTestDriver();
    const el = await this.getElement();
    let item;
    try {
      item = await el.waitForElement(byCss(`[data-oj-key="${key.toString()}"]`));
    } catch {
      throw new Error('The key specified is invalid or does not exist');
    }
    return driver.click(item!);
  }

  /**
   * Remove the item in the NavigationList
   * @param key The key of the NavigationListItem to remove
   * @returns Promise<void>
   */
  async doRemove<K extends string | number>(key: K) {
    const driver = this.getTestDriver();
    const el = await this.getElement();
    let removeIcon;
    try {
      removeIcon = await el.waitForElement(
        byCss(
          `[data-oj-key="${key.toString()}"] [data-oj-navigationlist-item-remove-icon="true"]>*`
        )
      );
    } catch {
      throw new Error('The key specified is invalid or does not exist');
    }
    return driver.click(removeIcon!);
  }
}
